.header {
  padding-top: 24px;

  &__container {
    display: flex;
    align-items: center;
  }

  &__btn-box {
    display: flex;
    align-items: center;
    margin-left: auto;
  }

  &__btn {
    display: inline-block;
    padding: 11px 65px;
    background: linear-gradient(149.3deg, #F3B75D 67.79%, #F35DED 89.45%);
    box-shadow: inset 0px 4px 33px rgba(255, 255, 255, 0.37);
    border-radius: 15px;
    font-family: var(--second-family);
    font-size: 14px;
    line-height: 30px;
    color: #2A2634;
    text-transform: uppercase;
    transition: color .2s ease;

    &:hover {
      background: linear-gradient(149.3deg, #F35DED 65.14%, #F3B75D 89.45%);
      color: #fff;
      transition: color .2s ease;
    }
  }
}

.logo {
  display: flex;
  align-items: center;

  &__moon {
    transition: transform .8s ease;
  }

  &:hover .logo__moon {
    transition: transform .3s ease;
    transform: rotate(20deg);
  }

  &__text {
    position: relative;
    right: 6px;
    transition: transform .5s ease;
  }

  &:hover .logo__text {
    transform: translateX(5px);
  }
}

.nav {
  margin-left: 240px;

  &__list {
    display: flex;
    align-items: center;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 15px;
    color: #C6C5C7;
    gap: 26px;
  }

  &__link {
    color: inherit;
    transition: color .2s ease;
    padding: 4px;

    &:hover {
      color: #fff;
    }
  }
}

.cart {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  width: 52px;
  height: 52px;
  border-radius: 10px;
  background-color: #2A2634;
  cursor: pointer;
  transition: background-color .2s ease;
  position: relative;
  margin-right: 32px;

  &:hover {
    background-color: #575266;
  }

  &__count {
    position: absolute;
    top: 7px;
    right: 7px;
    min-width: 18px;
    min-height: 18px;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #E455DF;
    font-family: var(--second-family);
    font-size: 11px;
  }
}

.cart:hover .cart__bell {
  animation: shake 1s ease;
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  20%, 80% {
    transform: translateX(-6px);
  }

  40%, 60% {
    transform: translateX(6px);
  }
}



