.hero {
  padding-top: 85px;
  padding-bottom: 50px;

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: var(--offset);
  }

  &__content {
    grid-column: 6 span;
  }

  &__img {
    grid-column: 6 span;
  }

  &__subtitle {
    display: inline-block;
    margin: 0;
    margin-bottom: 50px;
    font-family: var(--second-family);
    font-size: 16px;
    text-transform: uppercase;
    padding: 10px;
    background-color: #3E3A49;
    border-radius: 5px;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-family: var(--second-family);
    font-size: 48px;
  }

  &__descr {
    margin: 0;
    font-size: 20px;
  }

  &__btn {
    margin-top: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(149.3deg, #F35DED 65.14%, #F3B75D 89.45%);
    box-shadow: inset 0px 4px 33px rgba(255, 255, 255, 0.37);
    border-radius: 15px;
    font-family: var(--second-family);
    font-size: 16px;
    text-transform: uppercase;
    color: #FFFFFF;
    padding: 22px 76px 22px 52px;
    position: relative;
    transition: border-radius .3s ease;

    &::after {
      content: '';
      display: inline-block;
      position: absolute;
      left: 215px;
      width: 15px;
      height: 15px;
      background-image: url(../img/btn-arrow.svg);
      transition: transform .3s ease;
    }

    &:hover::after {
      transform: translateX(5px);
    }

    &:hover {
      background: linear-gradient(149.3deg, #F3B75D 67.79%, #F35DED 89.45%);
      border-radius: 20px;
      transition: border-radius .7s ease;
    }
  }
}

.hero__img {
  width: 100%;
  height: 100%;
}
